import IconLogo from 'components/IconLogo'

import ImageSlogan from 'pages/images/slogan.png'
import ImagePotato from 'pages/images/potato.png'
import ImageTg from 'pages/images/tg.png'

import ImageMain from './images/main.png'
import ImageDownloadIos from './images/download-ios.png'
import ImageDownloadAndroid from './images/download-android.png'
import IconArrowRight from './IconArrowRight'
import { Link } from 'react-router-dom'

function IndexPage() {

  const ios = /iPhone|iPad|iPod/.test(navigator.userAgent)

  return (
    <div className="IndexPage">
      <IconLogo />
      <img
        className="Slogan"
        src={ImageSlogan}
      />
      <img
        className="Main"
        src={ImageMain}
      />
      {
        ios ? (
          <a
            className="ButtonDownload"
            href={HuarsConfig.DOWNLOAD_IOS}
          >
            <img src={ImageDownloadIos} />
          </a>
        ) : (
          <a
            className="ButtonDownload"
            href={HuarsConfig.DOWNLOAD_ANDROID}
          >
            <img src={ImageDownloadAndroid} />
          </a>
        )
      }
      <Link
        className="LinkGuide"
        to="/guide"
      >
        查看安装教程
        <IconArrowRight />
      </Link>
      <div className="ContactUs">
        {/* <a target="_blank" href={HuarsConfig.POTATO_CHAT}>
          <img src={ImagePotato} />
        </a> */}
        <a target="_blank" href={HuarsConfig.TELEGRAM_CHAT}>
          <img src={ImageTg} />
        </a>
        <label>联系官方</label>
      </div>
    </div>
  )
}

export default IndexPage
